.header {
  position: sticky;
  left: 0;
  top: 0;
  z-index: 100;
  padding: 23px 0 15px;
  background-color: #FFF;
  animation: scrolling-header;
  animation-fill-mode: both;
  animation-range: 100px 200px;
  animation-timeline: scroll();

  @include small-tablet {
    padding: 18px 0;
  }

  @include mobile {
    padding: 14px 0;
  }

  @keyframes scrolling-header {
    to {
      box-shadow: 0 0 10px 0 #084bc9;
    }
  }

  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;

    @include tablet {
      gap: 10px;
    }
  }

  &__logo {
    @include tablet {
      width: 90px;
    }
  }

  &__city {
    display: flex;
    align-items: center;
    gap: 4px;

    @include small-tablet {
      display: none;
    }
  }

  &__svg {
    width: 20px;
    height: 21px;
    fill: #fff;
    stroke: #DA0202;
    stroke-width: 1.5px;
  }

  &__city-name {
    font-weight: 500;
    font-size: 15px;
    line-height: 130%;
    color: #333;
  }

  &__social {}

  &__social-list {
    display: flex;
    justify-content: space-around;
    margin-bottom: 5px;
    gap: 10px;

    @include tablet {
      gap: 5px;
    }

    @include mobile {
      margin-bottom: 0;
    }
  }

  &__social-item {}

  &__social-link {}

  &__social-svg {
    width: 28px;
    height: 28px;
    stroke: #fff;
    stroke-width: 0.8px;

    @include mobile {
      width: 22px;
      height: 22px;
    }
  }

  &__social-svg--tg {
    fill: #2FA8DF;
  }

  &__social-svg--wa {
    fill: #269F2E;
  }

  &__social-text {
    font-weight: 400;
    font-size: 12px;
    text-transform: lowercase;
    color: #333;

    @include small-tablet {
      font-size: 11px;
    }

    @include mobile {
      display: none;
      margin-bottom: 0;
    }
  }

  &__contacts {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;

    @include tablet {
      gap: 5px;
    }
  }

  &__contact {
    font-weight: 500;
    font-size: 18px;
    color: #333;

    @include desktop {
      font-size: 15px;
    }

    @include small-tablet {
      font-size: 13px;
    }

    span {
      @include mobile {
        display: none;
      }
    }
  }

  &__contact-text {
    font-weight: 500;
    font-size: 12px;
    text-align: center;
    text-transform: lowercase;
    color: #333;

    @include small-tablet {
      font-size: 11px;
    }

    @include mobile {
      display: none;
    }
  }

  &__contacts-svg {
    display: none;

    @include mobile {
      display: block;
      width: 19px;
      height: 19px;
      fill: none;
      stroke: #084bc9;
    }
  }

  &__btn {
    display: inline-block;
    border: 1.50px solid #da0202;
    border-radius: 93px;
    padding: 15px 28px;
    font-weight: 500;
    font-size: 14px;
    text-transform: uppercase;
    color: #da0202;

    @include tablet {
      font-size: 11px;
    }

    @include small-tablet {
      padding: 10px 15px;
      font-size: 10px;
    }

    @include mobile {
      display: none;
    }
  }
}
