.business {
  padding-bottom: 180px;

  &__content {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 30px;
  }

  &__cart {
    padding: 14px 35px 30px;
    width: 100%;
    min-height: 520px;
    background-image: url("../img/business-pic.svg");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;

// aspect-ratio: 0.84 / 1;
  }

  &__svg {
    display: block;
    margin: 0 auto;
    margin-bottom: 30px;
  }

  &__cart-title {
    margin: 0;
    margin-bottom: 30px;
    font-weight: 500;
    font-size: 34px;
    line-height: 110%;
    letter-spacing: -0.01em;
    text-align: center;
  }

  &__list {

  }

  &__item {
    font-weight: 400;
    font-size: 20px;
    line-height: 130%;
    color: #2d2c3d;

    &:not(:last-child) {
      margin-bottom: 10px;
    }
  }
}

.business-page {
  .business__content {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin-bottom: 120px;
  }
}
