.reviews-client {
  padding-bottom: 130px;

  &__descr {
    font-weight: 400;
    font-size: 36px;
    line-height: 130%;
    color: #2d2c3d;
  }

  &__content {
    display: grid;
    grid-template-columns: 225px 225px 1fr 1fr 1fr;
    grid-template-rows: 260px 260px;
    gap: 30px;

  }

  &__card {
    position: relative;
    border: 10px solid transparent;
    border-radius: 50px;
    background: var(--borderGradient);

    &:first-child {
      grid-column: 1 / 3;
      grid-row: 1 / 3;
      height: 490px;

      .reviews-client__name {
        padding: 17px 27px;
        font-weight: 600;
        font-size: 20px;
      }
    }
  }

  &__video {}

  &__name {
    margin: 10px 15px;
    border-radius: 30px;
    padding: 5px;
    font-weight: 500;
    font-size: 10px;
    line-height: 130%;
    color: #2d2c3d;
    background: rgba(255, 255, 255, 0.59);
  }

  &__prew {
    position: absolute;
    left: 50%;
    bottom: -90px;
    border-radius: 24px;
    padding: 30px;
    width: 100%;
    max-width: 396px;
    box-shadow: 0 0 1px 0 rgba(0, 0, 0, 0.1);
    font-weight: 600;
    font-size: 28px;
    line-height: 110%;
    text-align: center;
    background-color: #fff;
    translate: -50%;
  }

  &__prew-text {
    font-weight: 600;
    font-size: 28px;
    line-height: 110%;
    text-align: center;
  }
}
