.audit-page {
  padding-bottom: 130px;

  @include desktop {
    padding-bottom: 80px;
  }

  @include tablet {
    padding-bottom: 60px;
  }

  &__title {
    margin: 0;
    margin-right: auto;
    margin-bottom: 70px;
    margin-left: auto;
    max-width: 1150px;
    font-weight: 600;
    font-size: 48px;
    line-height: 110%;
    text-align: center;
    color: #2d2c3d;

    @include desktop {
      font-size: 36px;
    }

    @include small-tablet {
      margin-bottom: 30px;
      font-size: 24px;
    }
  }

  &__wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px;

    @include mobile {
      grid-template-columns: 1fr;
      gap: 20px;
    }
  }

  &__card {
    position: relative;
    display: flex;
    align-items: center;
    border-radius: 30px;
    padding: 60px;
    box-shadow: 0 0 1px 0 rgba(0, 0, 0, 0.1);
    background: rgba(255, 255, 255, 0.59);
    gap: 30px;

    @include desktop {
      padding: 40px;
    }

    @include tablet {
      padding: 24px;
    }

    &:last-child {
      grid-column: 2 span;

      @include mobile {
        grid-column: 1 span;
      }
    }
  }

  &__card-svg {
    position: absolute;
    right: 0;
    top: 0;
    width: 100px;
    height: 100px;

    @include mobile {
      width: 70px;
      height: 70px;
    }
  }

  &__content {}

  &__card-title {
    margin: 0;
    margin-bottom: 15px;
    font-weight: 600;
    font-size: 36px;
    line-height: 110%;
    color: #2d2c3d;

    @include desktop {
      font-size: 26px;
    }

    @include tablet {
      font-size: 24px;
    }

    @include small-tablet {
      font-size: 18px;
    }
  }

  &__descr {
    margin: 0;
    font-weight: 400;
    font-size: 24px;
    line-height: 130%;
    color: #2d2c3d;

    @include desktop {
      font-size: 20px;
    }

    @include tablet {
      font-size: 18px;
    }

    @include small-tablet {
      font-size: 14px;
    }
  }
}

.audit {
  &__video-inner {
    text-align: center;

    span {
      display: inline-block;
      margin-top: 30px;
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 20px;
      line-height: 90%;
      color: rgba(45, 44, 61, 0.8);

    }
  }

  &__video {
    flex-shrink: 0;
    border: 10px solid transparent;
    border-radius: 50px;
    width: 500px;
    height: 500px;
    background: var(--borderGradient);

    @include desktop {
      width: 400px;
      height: 400px;
    }

    @include tablet {
      border: 5px solid transparent;
      border-radius: 35px;
      width: 350px;
      height: 350px;
    }

    @include small-tablet {
      margin-left: 0;
      width: 250px;
      height: 250px;
    }

    @include mobile {
      width: 100%;
    }

    iframe {
      border-radius: 40px;
      width: 100%;
      height: 100%;

      @include tablet {
        border-radius: 30px;
      }
    }
  }
}
